@use "mixins"as *;

.breadcrumbs {
    margin-bottom: 44px;

    @media (max-width: 767px) {
        display: none;
    }

    &__wrapper {
        @include flex-wrapper-base;
        gap: 12px;
        width: fit-content;
    }

    &__separator {
        background: #fff;
        width: 6px;
        height: 6px;
        border-radius: 50%;
    }
}