@use "mixins"as *;

.footer {
    padding-top: 100px;

    @media (max-width: 767px) {

        padding-top: 56px;
    }

    &__container {}

    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

    }

    &__inner {
        display: flex;
        gap: 72px;

        @media (max-width: 1150px) {
            gap: 16px;
        }

        @media (max-width: 1023px) {
            flex-direction: column;
            gap: 24px;
        }

        @media (max-width: 767px) {
            gap: 14px;
        }
    }

    &__item {}

    &__logo {
        width: 185px;
        margin-bottom: 20px;

        @media (max-width: 767px) {
            width: 132px;
            margin-bottom: 6px;
        }

        img {
            width: 100%;
        }
    }

    &__btn {
        padding: 13px 27px;
        text-transform: uppercase;
        font-weight: 900;
        font-size: 12px;

        @media (max-width: 767px) {
            padding: 13px 22px;

        }
    }

    &__menu-list {
        list-style: none;
        display: flex;
        flex-direction: column;
        gap: 10px;
        font-size: 18px;
        font-weight: 400;

        @media (max-width: 767px) {
            gap: 8px;
            font-size: 12px;

        }
    }


    &__subtitle {
        font-size: 16px;
        font-weight: 600;
        margin-bottom: 12px;

        @media (max-width: 767px) {

            font-size: 14px;
            margin-bottom: 10px;
        }
    }

    &__socials {
        display: flex;
        gap: 10px;

        @media (max-width: 767px) {
            flex-direction: column;
        }
    }

    &__box {
        display: flex;
        align-items: center;
        gap: 10px;
        background-color: #061628;
        border-radius: 10px;
        padding: 10px 14px;

    }

    &__socials-icon {
        width: 42px;
        height: 42px;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__link {
        display: flex;
        flex-direction: column;
        gap: 5px;
    }

    &__name {
        font-size: 12px;
    }

    &__path {
        @include sub-font;
        font-size: 14px;
        font-weight: 400
    }

    &__box {}

    &__label {
        width: 40px;
        height: 40px;
        margin-bottom: 12px;

        &:last-child {
            width: 42px;
            height: 42px;
            margin-bottom: 0;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}

.footer-post {
    padding-top: 38px;
    padding-bottom: 12px;

    @media (max-width: 767px) {
        padding-top: 32px;
        padding-bottom: 10px;
    }

    @include flex-wrapper-base;

    gap: 62px;

    @media (max-width: 1600px) {
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        width: 100%;
        gap: 32px;
    }

    @media (max-width: 767px) {
        gap: 0;
    }

    &__item {
        @include flex-wrapper-base;
        width: 100%;

        @media (max-width: 1600px) {
            // justify-content: center;
            // gap: 124px;
        }

        // flex-wrap: wrap;
    }
}