@import "https://fonts.fontstorage.com/import/rubikone.css";

@mixin flex-wrapper-base {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@mixin base-font {
    font-family: "Inter", serif;
    font-optical-sizing: auto;
    font-style: normal;

}
@mixin banner-font {
    font-family: "Rubik", serif;
    font-optical-sizing: auto;
    font-style: normal;

}

@mixin sub-font {
    // font-family: "Rubik One Regular", sans-serif;
    font-family: 'Rubik One';

}