@use "mixins"as *;

.banner {
    margin-top: 94px;

    @media (max-width: 767px) {
        margin-top: 32px;
    }

    &__container {}

    &__wrapper {
        position: relative;
        width: 100%;
        height: 400px;
        display: flex;
        align-items: center;
        padding: 70px 143px 59px;
        border-radius: 30px;
        overflow: hidden;

        @media (max-width: 1023px) {
            padding: 12px 48px;
        }

        @media (max-width: 767px) {
            border-radius: 12px;
            padding: 48px 12px;
            height: 220px;
        }
    }

    &__img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 1;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;

            @media (max-width: 1600px) {
                object-fit: cover;
                object-position: center;
            }

            @media (max-width: 767px) {
                object-fit: cover;
                object-position: center;
            }

        }
    }

    &__inner {
        position: relative;
        z-index: 2;
    }

    &__text {
        @include banner-font;
        font-size: 24px;
        color: #FF9900;

        @media (max-width: 767px) {
            font-size: 14px;
        }
    }

    &__specific {
        @media (max-width: 1023px) {
            width: 700px;
        }

        @media (max-width: 767px) {
            width: 254px;
            margin-top: 8px;
        }

        img {
            width: 100%;
        }
    }

    &__btn {
        padding: 24px 48px;
        margin-top: 24px;
        font-size: 18px;
        text-transform: uppercase;
        font-weight: 700;

        @media (max-width: 767px) {
            font-size: 12px;
            margin-top: 14px;
            padding: 13px 17px;
        }
    }
}