@import "https://fonts.fontstorage.com/import/rubikone.css";
*,
::after,
::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Inter", serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-family: "Rubik One Regular", Arial, Helvetica, sans-serif;
  font-weight: 400;
  background: #030E1A;
  color: white;
}

a {
  color: inherit;
  text-decoration: none;
}

.current-link {
  color: #0077FF;
}

.base-container,
.sub-container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 16px;
}

.sub-container {
  max-width: 1860px;
}

.btn {
  background-color: #0077FF;
  color: #fff;
  border-radius: 35px;
  border: 1px solid #0077FF;
  position: relative;
}
.btn a::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.btn.btn-transparent {
  background-color: transparent;
  border: 1px solid #fff;
}

.title {
  font-size: 36px;
  text-align: center;
  padding: 64px 0;
  text-transform: uppercase;
  font-family: "Rubik One";
}
.title.pre-breadcrumbs {
  padding-bottom: 48px;
}
@media (max-width: 767px) {
  .title.pre-breadcrumbs {
    padding-bottom: 24px;
  }
}
@media (max-width: 767px) {
  .title {
    font-size: 16px;
    padding: 24px 0;
  }
}

.header {
  padding: 24px 0;
  position: relative;
}
.header__logo {
  width: 254px;
}
.header__logo img {
  width: 100%;
}
@media (max-width: 767px) {
  .header__logo {
    width: 132px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 12px 0;
  }
}
.header__wrapper, .header__btns {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}
.header__btn {
  padding: 13px 46px;
  font-size: 14px;
}
.header__btn:first-child {
  padding: 13px 34px;
}
@media (max-width: 767px) {
  .header__btn.hide-mobile {
    display: none;
  }
}
@media (max-width: 767px) {
  .header__btn {
    padding: auto;
    width: 100%;
    font-size: 12px;
  }
  .header__btn:first-child {
    padding: auto;
  }
}
.header__burger {
  width: 32px;
  height: 32px;
  background: transparent;
  border: none;
  outline: none;
}
@media (min-width: 767px) {
  .header__burger {
    display: none;
  }
}
.header__menu {
  background: black;
  top: 56px;
  position: fixed;
  width: 100vw;
  height: calc(100% - 56px + env(safe-area-inset-bottom));
  z-index: 4;
  padding: 32px 16px 16px;
  padding-bottom: calc(16px + env(safe-area-inset-bottom));
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.footer {
  padding-top: 100px;
}
@media (max-width: 767px) {
  .footer {
    padding-top: 56px;
  }
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.footer__inner {
  display: flex;
  gap: 72px;
}
@media (max-width: 1150px) {
  .footer__inner {
    gap: 16px;
  }
}
@media (max-width: 1023px) {
  .footer__inner {
    flex-direction: column;
    gap: 24px;
  }
}
@media (max-width: 767px) {
  .footer__inner {
    gap: 14px;
  }
}
.footer__logo {
  width: 185px;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .footer__logo {
    width: 132px;
    margin-bottom: 6px;
  }
}
.footer__logo img {
  width: 100%;
}
.footer__btn {
  padding: 13px 27px;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 12px;
}
@media (max-width: 767px) {
  .footer__btn {
    padding: 13px 22px;
  }
}
.footer__menu-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 18px;
  font-weight: 400;
}
@media (max-width: 767px) {
  .footer__menu-list {
    gap: 8px;
    font-size: 12px;
  }
}
.footer__subtitle {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 12px;
}
@media (max-width: 767px) {
  .footer__subtitle {
    font-size: 14px;
    margin-bottom: 10px;
  }
}
.footer__socials {
  display: flex;
  gap: 10px;
}
@media (max-width: 767px) {
  .footer__socials {
    flex-direction: column;
  }
}
.footer__box {
  display: flex;
  align-items: center;
  gap: 10px;
  background-color: #061628;
  border-radius: 10px;
  padding: 10px 14px;
}
.footer__socials-icon {
  width: 42px;
  height: 42px;
}
.footer__socials-icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.footer__link {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.footer__name {
  font-size: 12px;
}
.footer__path {
  font-family: "Rubik One";
  font-size: 14px;
  font-weight: 400;
}
.footer__label {
  width: 40px;
  height: 40px;
  margin-bottom: 12px;
}
.footer__label:last-child {
  width: 42px;
  height: 42px;
  margin-bottom: 0;
}
.footer__label img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.footer-post {
  padding-top: 38px;
  padding-bottom: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 62px;
}
@media (max-width: 767px) {
  .footer-post {
    padding-top: 32px;
    padding-bottom: 10px;
  }
}
@media (max-width: 1600px) {
  .footer-post {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 32px;
  }
}
@media (max-width: 767px) {
  .footer-post {
    gap: 0;
  }
}
.footer-post__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.breadcrumbs {
  margin-bottom: 44px;
}
@media (max-width: 767px) {
  .breadcrumbs {
    display: none;
  }
}
.breadcrumbs__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  width: fit-content;
}
.breadcrumbs__separator {
  background: #fff;
  width: 6px;
  height: 6px;
  border-radius: 50%;
}

.menu__list {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.menu__item {
  font-size: 16px;
}
.menu__link {
  font-size: inherit;
}

.banner {
  margin-top: 94px;
}
@media (max-width: 767px) {
  .banner {
    margin-top: 32px;
  }
}
.banner__wrapper {
  position: relative;
  width: 100%;
  height: 400px;
  display: flex;
  align-items: center;
  padding: 70px 143px 59px;
  border-radius: 30px;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .banner__wrapper {
    padding: 12px 48px;
  }
}
@media (max-width: 767px) {
  .banner__wrapper {
    border-radius: 12px;
    padding: 48px 12px;
    height: 220px;
  }
}
.banner__img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.banner__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}
@media (max-width: 1600px) {
  .banner__img img {
    object-fit: cover;
    object-position: center;
  }
}
@media (max-width: 767px) {
  .banner__img img {
    object-fit: cover;
    object-position: center;
  }
}
.banner__inner {
  position: relative;
  z-index: 2;
}
.banner__text {
  font-family: "Rubik", serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-size: 24px;
  color: #FF9900;
}
@media (max-width: 767px) {
  .banner__text {
    font-size: 14px;
  }
}
@media (max-width: 1023px) {
  .banner__specific {
    width: 700px;
  }
}
@media (max-width: 767px) {
  .banner__specific {
    width: 254px;
    margin-top: 8px;
  }
}
.banner__specific img {
  width: 100%;
}
.banner__btn {
  padding: 24px 48px;
  margin-top: 24px;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 700;
}
@media (max-width: 767px) {
  .banner__btn {
    font-size: 12px;
    margin-top: 14px;
    padding: 13px 17px;
  }
}

.benefits__cards {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
@media (max-width: 767px) {
  .benefits__cards {
    gap: 14px;
  }
}
.benefits__card {
  background-color: #061628;
  padding: 40px;
  width: calc(50% - 10px);
  border-radius: 24px;
}
@media (max-width: 767px) {
  .benefits__card {
    width: 100%;
    padding: 24px 12px;
    border-radius: 12px;
  }
}
.benefits__img {
  width: 78px;
  height: 73px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}
@media (max-width: 767px) {
  .benefits__img {
    width: 47px;
    height: 44px;
  }
}
.benefits__img img {
  width: 200%;
  position: absolute;
  left: -50%;
}
.benefits__subtitle {
  margin: 18px 0 10px;
  padding: 0;
  font-size: 24px;
  text-align: left;
}
@media (max-width: 767px) {
  .benefits__subtitle {
    font-size: 13px;
    margin: 16px 0 5px;
  }
}
.benefits__dsc {
  font-weight: 500;
  font-size: 18px;
  color: #e8e8e8;
}
@media (max-width: 767px) {
  .benefits__dsc {
    font-size: 12px;
  }
}

.be-partners {
  margin-bottom: 64px;
}
@media (max-width: 767px) {
  .be-partners {
    margin-bottom: 24px;
  }
}
.be-partners__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.be-partners__title {
  padding-bottom: 34px;
}
@media (max-width: 767px) {
  .be-partners__title {
    padding-bottom: 18px;
  }
}
.be-partners__btn {
  cursor: pointer;
  max-width: 318px;
  padding: 24px 61px;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .be-partners__btn {
    padding: 13px 38px;
    font-size: 12px;
    max-width: 207px;
  }
}

.about {
  padding-top: 106px;
}
@media (max-width: 991px) {
  .about {
    padding-top: 44px;
  }
}
.about__cards {
  display: flex;
  gap: 20px;
}
@media (max-width: 991px) {
  .about__cards {
    flex-direction: column;
    gap: 58px;
  }
}
.about__card {
  width: calc(33.3% - 10px);
  background-color: #061628;
  border-radius: 28px;
  padding: 76px 34px 27px;
  position: relative;
}
@media (max-width: 991px) {
  .about__card {
    width: 100%;
    padding: 32px 12px 18px;
    border-radius: 12px;
  }
}
.about__img {
  position: absolute;
  left: 0;
  top: -106px;
  width: 175px;
  height: 164px;
  display: flex;
  align-items: center;
}
@media (max-width: 991px) {
  .about__img {
    top: -40px;
    width: 72px;
    height: 67px;
  }
}
.about__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  filter: drop-shadow(10px 30px 20px rgba(89, 106, 252, 0.537254902));
}
.about__subtitle {
  padding: 0;
  text-align: left;
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 18px;
}
@media (max-width: 767px) {
  .about__subtitle {
    font-size: 13px;
    margin-bottom: 8px;
  }
}
.about__dsc {
  font-size: 18px;
  margin-bottom: 24px;
}
@media (max-width: 767px) {
  .about__dsc {
    font-size: 12px;
    margin-bottom: 8px;
  }
}
.about__list {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 18px;
  font-size: 16px;
}
.about__item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}
.about__pin {
  width: 25px;
  height: 23px;
}
@media (max-width: 767px) {
  .about__pin {
    width: 10px;
    height: 9px;
  }
}
.about__text {
  font-size: 16px;
}
@media (max-width: 767px) {
  .about__text {
    font-size: 10px;
  }
}

.faq__item {
  padding: 42px;
  background: #061628;
  margin-bottom: 20px;
  border-radius: 20px;
}
@media (max-width: 767px) {
  .faq__item {
    padding: 12px;
    border-radius: 8px;
  }
}
.faq__item:last-child {
  margin-bottom: 0;
}
.faq__inner {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 24px;
}
.faq__question {
  font-family: "Rubik One";
  font-weight: 500;
  font-size: 32px;
  text-transform: none;
  width: 100%;
}
@media (max-width: 767px) {
  .faq__question {
    font-size: 14px;
  }
}
.faq__answer {
  margin-top: 18px;
  font-size: 28px;
  font-weight: 400;
}
@media (max-width: 767px) {
  .faq__answer {
    margin-top: 8px;
    font-size: 12px;
  }
}
.faq__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  background: transparent;
  border: none;
  outline: none;
  width: 40px;
  height: 40px;
  transform: rotate(0deg);
  transition: transform 0.22s ease;
}
@media (max-width: 767px) {
  .faq__btn {
    width: 18px;
    height: 18px;
  }
}
.faq__btn img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media (max-width: 767px) {
  .faq__btn img {
    object-fit: cover;
  }
}
.faq__btn.open {
  transform: rotate(90deg);
}

.partner__title {
  text-transform: none;
}
.partner__wrapper ul {
  margin-top: 18px;
}
.partner__wrapper ul.numeric-decimals {
  counter-reset: section;
  list-style-type: none;
}
.partner__wrapper ul.numeric-decimals > li {
  list-style-type: none;
  counter-increment: section;
  font-size: 38px;
  font-weight: 600;
  margin-bottom: 18px;
  margin-top: 32px;
}
.partner__wrapper ul.numeric-decimals > li div {
  margin-top: 18px;
}
@media (max-width: 767px) {
  .partner__wrapper ul.numeric-decimals > li {
    font-size: 16px;
  }
}
.partner__wrapper ul.numeric-decimals > li:first-child {
  margin-top: 0;
}
.partner__wrapper ul.numeric-decimals > li:last-child {
  margin-bottom: 0;
}
.partner__wrapper ul.numeric-decimals > li:before {
  content: counter(section) ". ";
}
.partner__wrapper ul.numeric-decimals > li > ul {
  counter-reset: subsection;
  font-size: 28px;
  font-weight: 400;
  margin-bottom: 18px;
}
@media (max-width: 767px) {
  .partner__wrapper ul.numeric-decimals > li > ul {
    font-size: 12px;
  }
}
.partner__wrapper ul.numeric-decimals > li > ul > li {
  list-style-type: none;
  counter-increment: subsection;
  font-size: 28px;
  font-weight: 400;
  margin-bottom: 18px;
}
@media (max-width: 767px) {
  .partner__wrapper ul.numeric-decimals > li > ul > li {
    font-size: 12px;
  }
}
.partner__wrapper ul.numeric-decimals > li > ul > li:before {
  content: counter(section) "." counter(subsection) " ";
}
.partner__wrapper ul.numeric-decimals > li > ul > li > ul {
  list-style-type: disc;
  list-style-position: inside;
  padding-left: 40px;
  font-size: 28px;
  font-weight: 400;
}
@media (max-width: 767px) {
  .partner__wrapper ul.numeric-decimals > li > ul > li > ul {
    font-size: 12px;
    padding-left: 20px;
  }
}
.partner__wrapper ul.numeric-decimals > li > ul > li > ul li {
  margin-bottom: 18px;
}

.policy__title {
  text-transform: none;
}
.policy__wrapper {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
@media (max-width: 767px) {
  .policy__wrapper {
    gap: 14px;
  }
}
.policy__subtitle {
  font-size: 36px;
  margin-bottom: 18px;
  font-weight: 600;
}
@media (max-width: 767px) {
  .policy__subtitle {
    font-size: 16px;
    margin-bottom: 8px;
  }
}
.policy__text, .policy__list {
  font-size: 28px;
}
@media (max-width: 767px) {
  .policy__text, .policy__list {
    font-size: 12px;
  }
}
@media (max-width: 767px) {
  .policy__text.text-14 {
    font-size: 14px;
  }
}
.policy__list {
  padding: 0;
  padding-left: 40px;
  margin: 8px 0;
}
@media (max-width: 767px) {
  .policy__list {
    padding-left: 20px;
  }
}