@use "mixins"as *;

.header {
    padding: 24px 0;
    position: relative;


    &__logo {
        width: 254px;

        img {
            width: 100%;
        }

        @media (max-width: 767px) {
            width: 132px;
        }
    }

    @media (max-width: 767px) {
        padding: 12px 0;
    }

    &__wrapper,
    &__btns {
        @include flex-wrapper-base;
        gap: 10px;
    }


    &__btn { 
        padding: 13px 46px;
        font-size: 14px;

        &:first-child {
            padding: 13px 34px;
        }

        &.hide-mobile {
            @media (max-width: 767px) {
                display: none;
            }
        }

        @media (max-width: 767px) {
            padding: auto;
            width: 100%; 
            font-size: 12px;
            &:first-child {
                padding: auto;
            }

        }
    }

    &__burger {
        width: 32px;
        height: 32px;
        background: transparent;
        border: none;
        outline: none;

        @media (min-width: 767px) {
            display: none;
        }
    }

    &__menu {
        background: black;
        top: 56px;
        position: fixed;
        width: 100vw;
        height: calc(100% - 56px + env(safe-area-inset-bottom));
        z-index: 4;
        padding: 32px 16px 16px;
        padding-bottom: calc(16px + env(safe-area-inset-bottom));
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        // env(safe-area-inset-bottom)
    }


}