@use "variables"as *;
@use "mixins"as *;


*,
::after,
::before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    @include base-font;
    font-family: 'Rubik One Regular', Arial, Helvetica, sans-serif;
    font-weight: 400;
    background: $bg-color;
    color: $text-color;
}

a {
    color: inherit;
    text-decoration: none;
}



.current-link {
    color: $btn-bg;
}

.base-container,
.sub-container {
    max-width: 1600px;
    margin: 0 auto;
    padding: 0 16px;
}

.sub-container {
    max-width: 1860px;
}

.btn {
    background-color: $btn-bg;
    color: $btn-text-color;
    border-radius: 35px;
    border: 1px solid $btn-bg;
    position: relative;

    a {
        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
        }
    }

    &.btn-transparent {
        background-color: transparent;
        border: 1px solid $btn-text-color;
    }
}

.title {
    font-size: 36px;
    text-align: center;
    padding: 64px 0;
    text-transform: uppercase;
    @include sub-font;

    &.pre-breadcrumbs {
        padding-bottom: 48px;

        @media (max-width: 767px) {

            padding-bottom: 24px;
        }
    }

    @media (max-width: 767px) {
        font-size: 16px;
        padding: 24px 0;
    }
}